.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 20px;
  }
  .text {
    height: 24px;
    border-bottom: 1px solid var(--color-purple-100);
    color: var(--color-purple-100);
  }
  .social_container {
    display: flex;
    flex-direction: column;
    gap: 16px;
    align-items: center;
  }
  .btn_container {
    display: flex;
    justify-content: center;
    gap: 8px;
    height: 50px;
    transform: translateX(4px);
    pointer-events: all;
  }
  .season {
    background: rgba(53, 29, 173, 0.9);
    border-radius: 80px;
    color: var(--color-white);
    padding: 4px 16px;
    margin-top: 4px;
    font-weight: 700;
    font-size: 12px;
    line-height: 20px;
  }
  
  .con_pwa {
    position: absolute;
    bottom: 0;
    width: 100%;
    max-width: 500px;
    padding: 16px 20px;
    background: #ffffff;
    box-shadow: 0px 0px 16px rgba(18, 22, 25, 0.2);
    border-radius: 28px 28px 0px 0px;
    pointer-events: all;
  }
  
  .pwa_mainText {
    margin-bottom: 8px;
    font-weight: bold;
    font-size: 16px;
    line-height: 26px;
    color: #21272a;
  }
  
  .pwa_subText {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #565c63;
  }
  .pwa_con_logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-bottom: 20px;
  }
  
  .pwa_con_logo > img {
    width: 100px;
    border-radius: 15px;
  }
  
  .pwa_con_logo > div {
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    color: #21272a;
  }
  
  .pwa_con_btn {
    display: flex;
    gap: 12px;
  }
  
  .pwa_purple_btn {
    width: 100%;
    height: 54px;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    background: #5a39ff;
    box-shadow: 0px 6px 0px #351dad;
    border-radius: 40px;
  
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: #ffffff;
  }
  
  .pwa_white_btn {
    width: 100%;
    height: 54px;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    background: #ffffff;
    border: 1px solid #ebeff5;
    box-shadow: 0px 6px 0px #ebeff5;
    border-radius: 40px;
  
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: #21272a;
  }
  