.container {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: linear-gradient(186.09deg, #5a39ff 0%, #0046ff 95.18%);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.logo_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 16px;
}
.text_container {
  width: 100%;
  position: fixed;
  bottom: 16px;
  text-align: center;
  color: var(--color-white);
}
.logo {
  width: 140px;
}
.img_container {
  display: flex;
  height: 50px;
}
.left {
  transform: translateX(50%);
}
.right {
  align-self: flex-end;
  overflow: hidden;
  transform: translateX(-50%) rotate(180deg);
}

.load_text {
  position: fixed;

  text-align: center;
  color: var(--color-white);
}

.loading_bg {
  object-fit: cover;

  width: 100%;
  height: 100%;
}
